.footer {
	padding: 20px 0;
	color: $white;
	background-color: $dark;
	@include sm {
		padding: 24px 0 13px;
		text-align: center; }
	&__copy,
	&__logo {
		@include sm {
			float: none; } }
	&__copy {
		float: left;
		@include sm {
			margin-top: 10px; } }
	&__logo {
		float: right;
		@include sm {
			vertical-align: top;
			display: inline-block; } } }


.copy {
	opacity: 0.6;
	font-size: 11px;
	line-height: 1.73;
	font-weight: 700;
	@include font-smoothing; }
