.contact-us {
	&__inner {
		padding-top: 89px;
		padding-bottom: 104px;
		@include sm {
			padding-top: 30px;
			padding-bottom: 40px; } }
	&__row {
		margin: 0 -15px;
		@include clr;
		@include sm {
			margin: 0; } }
	&__col {
		float: left;
		padding: 0 15px;
		width: 50%;
		@include sm {
			padding: 0;
			width: 100%; } }
	&__content {
		padding-top: 15px;
		@include sm {
			padding: 0 0 25px; } }
	&__title {
		margin-bottom: 30px; }
	&__description {
		color: $dark;
		opacity: 0.9; } }

