.form {
	&__row {
		margin: 0 -15px;
		@include clr; }
	&__col {
		float: left;
		padding: 15px;
		width: 100%;
		&_md {
			width: 50%;
			@include xs {
				width: 100%; } } }
	&__footer {
		margin-top: -5px;
		text-align: right; }
	&__submit {
		position: relative;
		display: inline-block;
		vertical-align: top;
		padding: 0 14px;
		height: 40px;
		color: $dark;
		background: none;
		@include f(13, normal, 0.5, 600);
		text-transform: uppercase;
		@include sm {
			@include f(12, null, 0.4); }
		.icon {
			position: absolute;
			top: 50%;
			right: 0;
			margin-top: -5px;
			font-size: 10px; } } }
