.sm-info {
	color: $white;
	background: $dark;
	&__inner {
		padding-top: 90px;
		padding-bottom: 89px;
		@include sm {
			padding-top: 25px;
			padding-bottom: 25px; } }
	&__row {
		margin: 0 -115px;
		@include clr;
		@include lg {
			margin: 0 -50px; }
		@include md {
			margin: 0 -30px; }
		@include sm {
			margin: 0; } }
	&__col {
		float: left;
		padding: 30px 115px;
		width: 50%;
		@include lg {
			padding: 30px 50px; }
		@include md {
			padding: 30px; }
		@include sm {
			padding: 15px 0;
			width: 100%; } }
	&__header {
		margin-bottom: 30px;
		@include clr;
		@include sm {
			margin-bottom: 20px; } }
	&__icon {
		float: left;
		@include sm {
			float: none;
			padding-bottom: 20px; }
		&_home-agency {
			padding-right: 21px;
			margin-left: -12px;
			@include sm {
				padding-right: 0;
				margin: 0; } }
		&_home-outsourcing {
			padding-right: 27px;
			@include sm {
				padding-right: 0; } }
		.icon {
			font-size: 70px;
			@include sm {
				font-size: 35px; } } }
	&__title {
		overflow: hidden;
		max-width: 300px;
		@include sm;
		max-width: 160px; }
	&__description {
		br {
			@include sm {
				display: none; } } } }
