.hero {
	position: relative;
	@include cover;
	&__inner {
		padding-top: 310px;
		padding-bottom: 473px;
		color: $white;
		@include xl {
			padding-top: 240px;
			padding-bottom: 350px; }
		@include md {
			padding-top: 143px;
			padding-bottom: 140px; }
		@include sm {
			padding-top: 143px;
			padding-bottom: 106px; } }
	&__content {
		max-width: 470px;
		@include sm {
			max-width: 200px; } }
	&__title {
		margin-bottom: 46px;
		@include sm {
			margin-bottom: 21px; } } }
