body {
	color: $dark;
	background: $white;
	font-family: $lato;
	@include f(15, 1.27);
	&.is-hidden {
		overflow: hidden;
		width: 100%;
		height: 100%; } }

.out {
	overflow: hidden; }

.inner {
	position: relative;
	padding-left: 135px;
	padding-right: 135px;
	margin-left: auto;
	margin-right: auto;
	max-width: 1440px;
	@include clr;
	@include xl {
		padding-left: 70px;
		padding-right: 70px; }
	@include lg {
		padding-left: 40px;
		padding-right: 40px; }
	@include sm {
		padding-left: 20px;
		padding-right: 20px; } }
