.sm-product {
	&__img {
		position: absolute;
		top: 50%;
		right: 0;
		width: 789px;
		max-width: 60%;
		font-size: 0;
		transform: translate3d(0,-49%,0);
		pointer-events: none;
		@include md {
			position: relative;
			top: auto;
			right: auto;
			margin: 0 auto;
			max-width: 100%;
			@include gpu; }
		@include sm {
			margin: 0 -20px -10px;
			width: auto;
			max-width: unset; }
		img {
			width: 100%; } }
	&__content {
		padding: 63px 0 62px;
		max-width: 460px;
		@include md {
			margin: 0 auto; }
		@include sm {
			padding: 40px 0 18px; } }
	&__title {
		margin-bottom: 30px; }
	&__description {
		color: $dark;
		p + p {
			margin-top: 19px; } } }
