.sm-services {
	position: relative;
	color: $white;
	&__container {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		overflow: hidden;
		background: rgba($dark, 0.85);
		@include md {
			position: relative;
			background: $white; } }
	&__inner {
		padding: 0 !important; }
	&__item {
		position: relative;
		float: left;
		width: 25%;
		height: 234px;
		@include vertical;
		cursor: pointer;
		@include md {
			width: 50%;
			height: 130px;
			background: rgba($dark, 0.85); }
		@include sm {
			width: 50%;
			height: 91px; }
		@include hover {
			&:after {
				opacity: 1; }
			.sm-services {
				&__title {
					font-weight: 500; }
				&__description {
					position: relative;
					height: 3.75em;
					opacity: 0.9;
					visibility: visible; } } }
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			opacity: 0;
			background: rgba($dark, 0.54);
			box-shadow: 0 15px 53px 0 rgba(0, 0, 0, 0.48);
			transition: opacity 0.3s ease;
			@include md {
				display: none; } } }
	&__content {
		position: relative;
		z-index: 1;
		display: inline-block;
		vertical-align: middle;
		width: 100%; }
	&__icon {
		margin-bottom: 15px;
		height: 60px;
		@include vertical;
		@include md {
			margin-bottom: 10px; }
		@include sm {
			margin-bottom: 0;
			height: 39px; }
		img {
			display: inline-block;
			vertical-align: middle; } }
	&__design {
		width: 56px;
		@include md {
			width: 56/1.5+px; }
		@include sm {
			width: 28px; } }
	&__development {
		width: 71px;
		@include md {
			width: 71/1.5+px; }
		@include sm {
			width: 34px; } }
	&__acquisition {
		width: 52px;
		@include md {
			width: 52/1.5+px; }
		@include sm {
			width: 26px; } }
	&__retention {
		width: 58px;
		@include md {
			width: 58/1.5+px; }
		@include sm {
			width: 29px; } }
	&__title {
		margin-bottom: 15px;
		transition: font-weight 0.3s ease; }
	&__description {
		height: 0;
		opacity: 0;
		visibility: hidden;
		transition: height 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
		@include md {
			display: none; } } }
